<template>
    <div class="fillcontain">
        <div class="table_container">
          <el-row>
            <el-col :span="6">
              <el-input placeholder=" //" v-model="pages.keyword" class="input-with-select">
              </el-input>
            </el-col>
            <el-col :span="2">
              <el-button @click="initData"></el-button>
            </el-col>

          </el-row>
          <br/>
            <el-table
                :data="tableData"
                style="width: 100%"
                v-loading="loading">
              <el-table-column
                label=""
                prop="name"
                :show-overflow-tooltip="true"
              >
              </el-table-column>
              <el-table-column
                label=""
                prop="nickName">
              </el-table-column>
              <el-table-column
                label=""
                prop="user.account"
                :show-overflow-tooltip="true"
              >
              </el-table-column>
              <el-table-column
                label="()">
                <template slot-scope="scope">
                  {{scope.row.user.balance/100}}
                </template>
              </el-table-column>
              <el-table-column
                label=""
                :formatter="statusFormat">
              </el-table-column>
              <el-table-column label="" width="200">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEditRealPrice(scope.row)">
                    
                  </el-button>
                  <el-button
                    v-if="scope.row.black"
                    size="mini"
                    @click="blacklist(scope.row)">
                    
                  </el-button>
                  <el-button
                    v-if="!scope.row.black"
                    size="mini"
                    @click="blacklist(scope.row)">
                    
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="Pagination">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="pages.page"
                  :page-size="pages.pageSize"
                  layout="total, prev, pager, next"
                  :total="pages.total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                loading:false,
                call_tag:{
                    id:0,
                    content:"",
                    status:""+1,
                },
                dialog:{
                    dialogFormVisible:false,
                    title:"",
                    close:false,
                },
                pages:{
                    offset: 0,
                    pageSize: 10,
                    page: 1,
                    total: 0,
                },
              tableData: [],
            }
        },
        created(){
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/store/consumers', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.tableData = res.data.consumers.content;
                    that.pages.total = res.data.consumers.totalElements;
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },

            handleSizeChange(val) {
                console.log(` ${val} `);
            },
            handleCurrentChange(val) {
                this.pages.page = val;
                this.pages.offset = (val - 1)*this.pages.pageSize;
                this.initData()
            },
            statusFormat(row,column){
              return (row.user.isFreeze == 0 && row.black == false)?"":"";
            },
          blacklist(row) {
            var that = this;
            that.$api.get('/store/blacklist', {user_id:row.user.id}, response => {
              if (response.status >= 200 && response.status < 300) {
                console.log(response.data);
                var res = response.data;
                if (res.code == 0) {
                  if(res.data.blacklists.content.length > 0){
                      this.$confirm('?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                      }).then(() => {

                        this.loading = true;
                        that.$api.delete('/store/blacklist', {user_id: row.user.id}, response => {
                          if (response.status >= 200 && response.status < 300) {
                            console.log(response.data);
                            var res = response.data;
                            if (res.code == 0) {
                              this.$message({
                                message: ",",
                                type: 'success'
                              });
                              this.initData();
                            } else {
                              this.$message({
                                message: res.msg,
                                type: 'error'
                              });
                            }

                            this.loading = false;
                          } else {
                            this.$message({
                              message: "",
                              type: 'error'
                            });

                            this.loading = false;
                          }
                        });

                      }).catch(() => {

                      });
                  } else {
                    this.$confirm('?', '', {
                      confirmButtonText: '',
                      cancelButtonText: '',
                      type: 'warning'
                    }).then(() => {

                      this.loading = true;
                      that.$api.post('/store/blacklist', {user_id: row.user.id}, response => {
                        if (response.status >= 200 && response.status < 300) {
                          console.log(response.data);
                          var res = response.data;
                          if (res.code == 0) {
                            this.$message({
                              message: ",",
                              type: 'success'
                            });
                            this.initData();
                          } else {
                            this.$message({
                              message: res.msg,
                              type: 'error'
                            });
                          }

                          this.loading = false;
                        } else {
                          this.$message({
                            message: "",
                            type: 'error'
                          });

                          this.loading = false;
                        }
                      });

                    }).catch(() => {

                    });

                    this.loading = false;
                  }
                }
              } else {
                this.$message({
                  message: "",
                  type: 'error'
                });

                this.loading = false;
              }
            });
          },
          handleEditRealPrice(row){
            var that = this;
            this.$prompt('', '', {
              confirmButtonText: '',
              cancelButtonText: '',
            }).then(({ value }) => {
              that.$api.post('/store/consumers/'+row.id, {chargePrice:value*100}, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.initData();
                    this.$message({
                      message: "",
                      type: 'success'
                    });
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "",
                    type: 'error'
                  });
                  this.initData();
                }
                this.loading = false;
              });

            }).catch(() => {

            });
          },

        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



