<template>
    <div class="fillcontain">
        <div class="table_container">
          <el-divider content-position="left"></el-divider>
            <el-row>
              <el-col :span="2"></el-col><el-col :span="4">{{order.orderNo}}</el-col>
              <el-col :span="2"></el-col><el-col :span="4">{{order.createTimeStr}}</el-col>
              <el-col :span="2"></el-col><el-col :span="4">{{order.createdUser.account}}</el-col>
            </el-row>
            <br/>
            <el-row>
              <el-col :span="2"></el-col><el-col :span="4">
              <span v-if="order.type == 1">
                ,:{{order.tableNumber}}
              </span>
              <span v-else="order.type == 3">
                
              </span>
            </el-col>
              <el-col :span="2"></el-col><el-col :span="4">{{order.applyStatusStr}}</el-col>
              <el-col :span="2"></el-col><el-col :span="4">{{order.peopleNum}}</el-col>
            </el-row>
            <br/>
            <el-row>
              <el-col>: {{order.note}}</el-col>
            </el-row>
          <el-divider content-position="left"></el-divider>
            <el-row>
              <el-col :span="3"></el-col><el-col :span="4"> {{order.totalPrice/100}}</el-col>
              <el-col :span="3"></el-col><el-col :span="4">{{order.realPrice/100}}</el-col>
            </el-row>
            <br/>
            <el-row>
              <el-col :span="2">
                </el-col><el-col :span="4"> {{order.payStatusStr}}<span v-if="order.applyStatus == 3">()</span>
                <span v-if="order.payStatus == 3 || order.payStatus == 4">:{{order.refundNote}}</span>
            </el-col>

              <el-button
                size="mini"
                v-if="order.payStatus == 1 && order.applyStatus != 3"
                @click="handlePayAlert(order)">
                
              </el-button>

              <el-button
                size="mini"
                v-if="order.payStatus == 1 && order.applyStatus != 3"
                @click="handleOnlinePayAlert(order)">
                
              </el-button>




              <el-col :span="2" v-if="order.payStatus > 1"></el-col><el-col :span="4" v-if="order.payStatus > 1"> {{order.payTimeStr}}</el-col>
              <el-col :span="2" v-if="order.payStatus > 1"></el-col><el-col :span="4" v-if="order.payStatus > 1">{{order.payTypeStr}}<el-button size="mini" v-if="order.payStatus == 2" @click="handleRefundOrderAlert(order)"></el-button>

            </el-col>
              <el-col :span="2" v-if="order.payStatus > 1"></el-col><el-col :span="4" v-if="order.payStatus > 1">{{order.payUser.account}}</el-col>

            </el-row>


            <br/>
          <br/>

          <el-table
            v-if="order.type == 1"
            :data="goodsData"
            style="width: 100%"
            v-loading="loading">
            <el-table-column
              label=""
              prop="goodsName">
            </el-table-column>

            <el-table-column
              label=""
              prop="goods.number">
            </el-table-column>

            <el-table-column
              label=""
              :formatter="markerPriceFormat">
            </el-table-column>

            <el-table-column
              label=""
              :formatter="sellPriceFormat">
            </el-table-column>

            <el-table-column
              label=""
              prop="count">
            </el-table-column>

            <el-table-column
              label=""
              prop="note"
              :show-overflow-tooltip="true"
            >
            </el-table-column>

            <el-table-column
              label=""
              :formatter="statusFormat">
            </el-table-column>

            <el-table-column label="" width="200">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  v-if="(order.payStatus == 2 || order.payStatus == 3) && scope.row.payStatus == 1"
                  @click="handleRefundAlert(scope.row)">
                  
                </el-button>
              </template>
            </el-table-column>
          </el-table>




          <el-dialog title="" :visible.sync="payTypeDialog" width="90%" :show-close="false" :close-on-click-modal="false" :close-on-press-escape="false">
            <p v-if="paymentConfigs.length == 0" style="font-size: 30px">
              <span >,</span>
            </p>
            <p v-else>
              <a :href="baseUrl+'/payment/pay/'+item.id+'?order_id='+order.id+'&token='+token" target="_blank" v-for="(item,key) in paymentConfigs" :key="key"><el-button type="success">{{item.name}}</el-button></a>
            </p>
            <div slot="footer" class="dialog-footer">
              <el-button type="primary" @click="closePayPop()">  </el-button>
            </div>
          </el-dialog>
        </div>



    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                uploadUrl:baseUrl+'/common/imageUpload',
                loading:false,
                order_id:0,
                order:{
                      id:0,
                      number:"",
                      createTimeStr:"",
                      payTime:"",
                      payTimeStr:""
                  },
              goodsData: [],
              totalPrice:0,
              keyword:"",

              chooseOrderGoods:{},
              noteDialogTableVisible:false,
              paymentConfigs:[],

              payTypeDialog:false,
              token:null,
            }
        },
        created(){
            //
          var storage = window.localStorage;
          this.token = storage["access_token"];
          this.order_id = this.$route.query.order_id;
          this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/store/orders', {order_id:this.order_id}, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.order = res.data.orders.content[0];
                    that.order.createTimeStr = this.dateTimeFormat(that.order.createTime);
                    if(that.order.payTime != null && that.order.payTime != ""){
                      that.order.payTimeStr = this.dateTimeFormat(that.order.payTime);
                    }

                    that.initGoodsData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },
            initGoodsData(){
              this.loading = true;
              var that = this;
              that.$api.get('/store/orders/orderGoods', {order_id:that.order.id}, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.goodsData = res.data.orderGoods;
                    that.totalPrice = 0;
                    that.goodsData.forEach(function(value,i){
                      that.totalPrice += (value.goods.sell_price * value.count);
                    });
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },
          handleRefundAlert(row) {
            var that = this;
            this.$prompt('(payjs)', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              inputPlaceholder:""
            }).then(({ value }) => {
              if(value.trim() == ""){
                that.$message({
                  message: ',',
                  type: 'error'
                });
              }else{
                this.handleRefund(row,value);
              }

            }).catch(() => {

            });
          },
          handleRefund(row,note) {
            this.loading = true;
            var that = this;
            that.$api.put('/store/orders/order_goods/'+row.id+'/refund', {note:note}, response => {
              if (response.status >= 200 && response.status < 300) {
                console.log(response.data);
                var res = response.data;
                if (res.code == 0) {
                  this.$message({
                    message: '',
                    type: 'success'
                  });
                  this.initData();
                }else{
                  this.$message({
                    message: res.msg,
                    type: 'warning'
                  });
                }

                this.loading = false;
              } else {
                this.$message({
                  message: "",
                  type: 'error'
                });

                this.loading = false;
              }

            });
          },
          handleRefundOrderAlert(row) {
              var that = this;
            this.$prompt(',', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              inputPlaceholder:""
            }).then(({ value }) => {
              if(value.trim() == ""){
                that.$message({
                  message: ',',
                  type: 'error'
                });
              }else{
                this.handleRefundOrder(row,value);
              }

            }).catch(() => {

            });


            // this.$confirm(',', '', {
            //   confirmButtonText: '',
            //   cancelButtonText: '',
            //   type: 'warning'
            // }).then(() => {
            //   this.handleRefundOrder(row);
            // }).catch(() => {
            //
            // });
          },
          handleRefundOrder(row,note) {
            this.loading = true;
            var that = this;
            that.$api.put('/store/orders/'+row.id+'/refund', {note:note}, response => {
              if (response.status >= 200 && response.status < 300) {
                console.log(response.data);
                var res = response.data;
                if (res.code == 0) {
                  this.$message({
                    message: '',
                    type: 'success'
                  });
                  this.initData();
                }else{
                  this.$message({
                    message: res.msg,
                    type: 'warning'
                  });
                }

                this.loading = false;
              } else {
                this.$message({
                  message: "",
                  type: 'error'
                });

                this.loading = false;
              }

            });
          },
            statusFormat(row,column){
                //return row.status == 2?"":"";
              return row.payStatus == 2?"":"";
            },
            markerPriceFormat(row,column){
              return row.goods.marker_price/100;
            },
            sellPriceFormat(row,column){
              return row.goods.sell_price/100;
            },
            totalPriceFormat(row,column){
              return row.goods.sell_price/100;
            },

          handlePayAlert(row) {
            this.$confirm('?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.loading = true;
              var that = this;
              that.$api.put('/store/orders/'+row.id+'/pay', null, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    this.$message({
                      message: '',
                      type: 'success'
                    });
                    this.initData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });

                    this.loading = false;
                  }
                } else {
                  this.$message({
                    message: "",
                    type: 'error'
                  });

                  this.loading = false;
                }

              });
            }).catch(() => {

            });
          },

          //
          handleOnlinePayAlert(row){
            this.order = row;
            this.loading = true;
            var that = this;
            that.$api.get('/store/payment_config/can_used',null, response => {
              if (response.status >= 200 && response.status < 300) {
                var res = response.data;
                if (res.code == 0) {
                  that.payTypeDialog = true;
                  that.paymentConfigs = res.data.paymentsConfigs;
                  console.log(response);
                }else{
                  //this.$toast.error(res.msg)
                }
              } else {
                //this.$toast.error('')
              }
              this.loading = false;
            });
          },
          closePayPop(){
              this.payTypeDialog = false;
              this.initData();
          },
          dateTimeFormat(time) {
            var date=new Date(time);
            var year=date.getFullYear();
            /* 00
             * 100   09:11:05
             * */
            var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
            var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
            var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
            var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
            var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
            // 
            return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
            //return year+"-"+month+"-"+day;
          },
            in_array(search,array){
              for(var i in array){
                if(array[i]==search){
                  return true;
                }
              }
              return false;
            },



        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



